import Component from "vue-class-component";
import axios from "axios";
import * as indexApi from "../../api/indexApi";
import moment from "moment";
import { Prop } from "vue-property-decorator";
import  prescriptioninfo from "../transmit/prescriptioninfo";
import  updateorder from "./updateorder";

@Component({
  props: {},
  components: {
      prescriptioninfo,updateorder
  }
})
export default class AddGoods extends Vue {
 @Prop({ required: false })
  orderList: any;
   @Prop({ required: false })
  pagetype: string;

order = {}

doReminder(row){
  
 this.$confirm("确定要发送一次催单?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
    indexApi
      .reminderByPresId({
          presId:row.presId
      })
      .then(res => {
   
        if (res["retCode"]) {
          this.$message("催单成功");
        } else {
          if(!res['islogin']){this.$alert(res["message"]);}
          console.error("数据查询错误");
        }
      });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "已取消操作"
        });
      });

}

getorderInfo(row){

 
//  let order = {}
//   Object.assign(order,row);
//   this.order = order
  let a : any =  this.$refs.updateorder
 a.model = true
//  a.queryPresDrugback(row.presId)
this.getOrderDetail(row.presId);
}
getOrderDetail(presId){
  indexApi
      .getOrderInfo({
          presId:presId
      })
      .then(res => {

        if (res["retCode"]) {
          console.log(' 订单详情',res.data)
  this.order = res.data
        } else {
          if(!res['islogin']){this.$alert(res["message"]);}
          console.error("数据查询错误");
        }
      });
}
updateOrder ={}

updateInfo(row){
 let updateOrder = {}
  Object.assign(updateOrder,row);
  this.updateOrder = updateOrder
  let a : any =  this.$refs.updateorder
 a.formModel = true
}



getOrderList(){
    this.$emit('getOrderList');
}
handlePayStatus (status){


switch(status){
 case 'PAY_WAIT':
  return '支付成功';
 case 'PAY_SUCCESS':
  return '支付成功';
  default :
  return '';
}
  
}

prescriptioninfoObj = {}
  preImageList = [];
  getTransmitInfo(index, row) {
    this.prescriptioninfoObj = row
let a:any = this.$refs.prescriptioninfo
a.getInfo();
  }
handleOrderStatus(status){

switch(status){
  case 'ORDER_INIT':
  return '订单初始化';
   case 'ORDER_WAIT_PAY':
  return '等待支付';
   case 'ORDER_PAY_ONDEV':
  return '货到付款';
   case 'ORDER_CANCEL_PAY':
  return '取消支付';
   case 'ORDER_WAIT_SENDGOODS':
  return '待发货';
     case 'ORDER_WAIT_RECVGOODS':
  return '待收货';
  case 'ORDER_END_GOODS':
